// comments

.comments {
  font-size: 0.875em;
  line-height: 2.15;

  @media (min-width: @tablet-width) {
    font-size: 1.125em;
    line-height: 1.66;
  }
}

.comments__content {
  padding-top: 38px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 80px;

  @media (min-width: @tablet-width) {
    padding-top: 67px;
    padding-bottom: 35px;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    .centered();
    width: 960px;
    padding-top: 186px;
    padding-right: 10px;
    padding-left: 10px;

    &::before {
      content: "";
      position: absolute;
      top: 73px;
      left: 50%;
      width: 79px;
      height: 63px;
      background-image: url("../img/comment-quotes.svg");
      background-repeat: no-repeat;
      background-size: 79px 63px;
      background-position: 0 0;
      transform: translateX(-50%);
    }
  }
}
