// dots
.dots {
  position: absolute;
  z-index: 505;
  width: 58px;
  min-height: 14px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.dots__dot {
  position: relative;
  padding: 0;
  width: 6px;
  height: 6px;
  border: none;
  border-radius: 50%;
  background-color: @basic-neutral;
  cursor: pointer;

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    left: -5px;
    width: 15px;
    height: 15px;
    background-color: transparent;
  }
}

// states

.dots__dot--active {
  position: relative;
  background-color: @basic-white;

  &::before {
    content: "";
    position: absolute;
    top: -2px;
    left: -2px;
    width: 10px;
    height: 10px;
    border: 2px solid @basic-black;
    border-radius: 50%;
  }
}
