// features

.features {
  position: relative;
  font-size: 0.875em;
  line-height: 1.72;
  background-color: @page-gray;

  &::before {
    top: -66px;
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: -37px;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      top: -89px;
    }
  }
}

.features__content {
  position: relative;
  min-height: 758px;
  padding-top: 55px;
  padding-right: 20px;
  padding-left: 20px;

  &::before {
    content: "";
    position: absolute;
    display: block;
    left: calc(50% - 160px / 2);
    bottom: 0;
    width: 157px;
    height: 285px;
    background-image: url("../img/features-icon-3.png");
    background-repeat: no-repeat;
    background-size: 157px 320px;
    background-position: 2px 8px;
    overflow: hidden;
  }

  @media (min-width: @tablet-width) {
    min-height: 475px;
    padding-top: 72px;
    padding-bottom: 43px;

    &::before {
      top: -10px;
      bottom: auto;
      left: 20px;
      width: 268px;
      height: 492px;
      background-image: url("../img/features-icon-3-tablet.png");
      background-size: 268px 547px;
      background-position: 0 11px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    min-height: auto;
    .centered();
    padding-top: 18px;
    padding-right: 10px;
    padding-left: 10px;

    &::before {
      display: none;
    }
  }
}

.features__list {
  .mp-reset();
  list-style: none;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: 50%;
    margin-left: auto;
    text-align: left;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    width: auto;
  }
}

.features__item {
  margin-bottom: 32px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 36px;
  }

  @media (min-width: @desktop-width) {
    width: 300px;
    margin-bottom: 0;
  }
}
