// footer

.footer {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
  }
}

.footer__content {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    min-height: 48px;
    padding-top: 33px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 37px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
    padding-top: 73px;
    padding-right: 10px;
    padding-bottom: 72px;
    padding-left: 10px;
  }
}

.footer__logo {
  display: block;
  width: 145px;
  align-self: flex-start;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__social {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 28.5%;
  .mp-reset();
  padding-top: 4px;
  list-style: none;

  @media (min-width: @desktop-width) {
    padding-top: 3px;
    width: 180px;
  }
}

.footer__link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 51px;
  height: 51px;
  border: 3px solid @basic-light;
  border-radius: 50%;

  &:hover {
    border-color: @basic-neutral;
  }

  &:active {
    opacity: 0.3;
  }
}

.footer__icon {
  fill: @page-red;
}

.footer__copy {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 150px;
  padding-left: 21px;
  font-size: 0.875em;
  line-height: 1.28;

  @media (min-width: @tablet-width) {
    width: 150px;
  }

  @media (min-width: @desktop-width) {
    width: 153px;
    padding-left: 0;
    font-size: 1.125em;
    line-height: 1;
  }
}

.footer__copy-link {
  display: block;
  width: 27px;
  height: 34px;
}

.footer__copy-icon {
  fill: @page-red;

  &:hover {
    fill: @basic-black;
  }

  &:active {
    opacity: 0.3;
  }
}
