// slogan

.slogan {
  padding-top: 41px;
  padding-right: 20px;
  padding-bottom: 60px;
  padding-left: 20px;
  color: @basic-black;

  @media (min-width: @tablet-width) {
    padding-top: 47px;
    padding-bottom: 50px;
  }

  @media (min-width: @desktop-width) {
    .centered();
    width: 960px;
    padding-top: 68px;
    padding-right: 10px;
    padding-bottom: 55px;
    padding-left: 10px;
  }
}

.slogan__title {
  position: relative;
  z-index: 505;
  text-align: center;
}

.slogan__separator {
  display: block;
  width: 100%;
  margin-bottom: 30px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}
