// info

.info__content {
  padding-bottom: 44px;
}

.info__group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 23px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    flex-direction: row;
    align-items: center;
    margin-bottom: 52px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 53px;
  }
}

.info__label {
  margin-bottom: 12px;
  padding-left: 23px;

  @media (min-width: @tablet-width) {
    margin-right: 19px;
    margin-bottom: 0;
    padding-left: 0;
  }
}

.info__input {
  @media (min-width: @tablet-width) {
    flex-grow: 1;
  }
}
