// header

.header {
  position: absolute;
  z-index: 506;
  width: 100%;
  font-size: 1.125em;
  line-height: 1.7;
  color: @basic-white;
  background-color: rgba(0, 0, 0, 0.3);
  word-break: break-all;
}

.header__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  .centered();

  @media (min-width: @desktop-width) {
    width: 960px;
    padding-top: 26px;
    padding-right: 10px;
    padding-bottom: 32px;
    padding-left: 10px;
  }
}

.header__head {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  min-width: 320px;
  width: 100%;
  padding-right: 20px;
  padding-top: 21px;
  padding-bottom: 26px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    padding-top: 28px;
    padding-bottom: 41px;
  }

  @media (min-width: @desktop-width) {
    min-width: auto;
    width: auto;
    padding: 0;
  }
}

.header__logo {
  @media (min-width: @tablet-width) {
    picture img {
      width: 147px;
      height: 40px;
    }
  }
}

.header__navigation {
  width: 100%;

  @media (min-width: @desktop-width) {
    width: 621px;
  }
}

// states

.header--active,
.header--nojs {
  position: static;
  background-color: @page-blue;
}
