// form-contacts

.form-contacts {
  @media (min-width: @tablet-width) {
    border: 2px solid @page-gray;
    border-radius: 2px;
  }
}

.form-contacts__title {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    width: 260px;
    text-align: center;
  }
}

.form-contacts__content {
  padding-top: 32px;
  padding-bottom: 40px;

  @media (min-width: @tablet-width) {
    padding-top: 30px;
    padding-right: 58px;
    padding-bottom: 28px;
    padding-left: 58px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 28px;
    padding-bottom: 15px;
  }
}

.form-contacts__group {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  margin-bottom: 23px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 30px;
  }

  @media (min-width: @desktop-width) {
    width: 375px;
    margin-bottom: 0;
  }
}

.form-contacts__label {
  margin-bottom: 12px;
  padding-left: 24px;

  @media (min-width: @tablet-width) {
    position: relative;
    order: 1;
    margin-bottom: 0;
    padding-left: 19px;

    &::before {
      content: "";
      position: absolute;
      top: -72px;
      right: 0;
      width: 60px;
      height: 60px;
      background-color: @basic-light;
      background-position: center;
      background-repeat: no-repeat;
    }

    &--phone {
      &::before {
        background-image: url("../img/icon-form-phone.svg");
        background-size: 20px 19px;
      }
    }

    &--mail {
      &::before {
        background-image: url("../img/icon-form-mail.svg");
        background-size: 18px 13px;
      }
    }
  }
}

.form-contacts__input {
  @media (min-width: @tablet-width) {
    margin-bottom: 10px;
  }
}
