// contacts

.contacts__content {
  position: relative;
  padding-top: 42px;
  padding-right: 20px;
  padding-bottom: 30px;
  padding-left: 20px;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    z-index: 507;
    left: 50%;
    bottom: -36px;
    width: 320px;
    height: 40px;
    background-image: url("../img/triangle-white-footer.svg");
    background-size: 320px 40px;
    background-repeat: no-repeat;
    background-position: 0 0;
    transform: translateX(-50%);
  }

  @media (min-width: @tablet-width) {
    padding-top: 45px;
    padding-bottom: 28px;

    &::before {
      bottom: -70px;
      width: 660px;
      height: 70px;
      background-size: 660px 70px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
    padding-top: 64px;
    padding-right: 10px;
    padding-bottom: 15px;
    padding-left: 10px;

    &::before {
      bottom: -115px;
      width: 960px;
      height: 120px;
      background-size: 960px 120px;
    }
  }
}

.contacts__title {
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    width: 70%;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 43px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 55px;
  }
}

.contacts__features {
  .mp-reset();
  list-style: none;
  line-height: 1.361;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.contacts__info {
  margin-bottom: 21px;

  &:last-child {
    margin-bottom: 0;
  }

  @media (min-width: @tablet-width) {
    width: 48%;
    margin-bottom: 45px;

    &:last-child {
      .centered();
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 0;
    width: 300px;

    &:nth-child(2) {
      order: 1;
    }

    &:last-child {
      margin-right: 0;
      margin-left: 0;
    }
  }
}

.contacts__sub-title {
  font-size: 1.125em;
  font-weight: 700;

  @media (min-width: @tablet-width) {
    margin-bottom: 3px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 20px;
  }
}

.contacts__link {
  font-size: 1.5em;
  font-weight: 300;
  color: @page-red;

  &:hover {
    color: @basic-black;
  }

  &:active {
    opacity: 0.3;
  }
}
