// toggle

.toggle-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;

  &:hover .toggle-nav__icon {
    fill: @page-red;
  }

  &:active .toggle-nav__icon {
    fill: @page-red;
    opacity: 0.3;
  }

  @media (min-width: @desktop-width) {
    display: none;
  }
}

.toggle-nav__icon {
  fill: @basic-white;
}

// icon states

.toggle-nav__icon-menu {
  display: block;
}

.toggle-nav__icon-cross {
  display: none;
}

.header--active .toggle-nav__icon-menu {
  display: none;
}

.header--active .toggle-nav__icon-cross {
  display: block;
}
