// button

.button {
  display: block;
  // min-width: 281px;
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-right: 5px;
  padding-bottom: 17px;
  padding-left: 5px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  color: @basic-white;
  text-decoration: none;
  text-align: center;
  background-color: transparent;
  border: 2px solid @basic-white;
  border-radius: 25px;

  &:hover {
    background-color: @basic-white;
    color: @page-red;
  }

  &:active {
    color: #f1becc;
  }

  @media (min-width: @desktop-width) {
    font-size: 21px;
  }
}
