// contest

.contest {
  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
  }
}

.contest__header {
  min-height: auto;
  padding-top: 40px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 43px;
  font-size: 0.875em;
  line-height: 2.14;

  @media (min-width: @tablet-width) {
    padding-right: 50px;
    padding-left: 50px;
    padding-bottom: 73px;
    font-size: 1.125em;
    line-height: 1.66;
  }

  @media (min-width: @desktop-width) {
    min-height: 196px;
    padding-right: 100px;
    padding-bottom: 64px;
    padding-left: 100px;
  }
}

.contest__hidden {
  display: none;

  @media (min-width: @tablet-width) {
    display: inline;
  }
}

.contest__form {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;

  @media (min-width: @tablet-width) {
    padding-right: 20px;
    padding-left: 20px;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding-right: 10px;
    padding-left: 10px;
  }
}

.contest__fieldset {
  width: 100%;

  @media (min-width: @tablet-width) {
    margin-bottom: 52px;

    &:nth-child(2) {
      order: 4;
    }

    &:nth-child(3) {
      order: 3;
    }

    &:nth-child(4) {
      order: 2;
    }

    &:nth-child(5) {
      order: 5;
      margin-bottom: 66px;
    }

    &:nth-child(6) {
      order: 5;
      margin-bottom: 42px;
    }
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 51px;

    &:first-child {
      width: 434px;
      padding-top: 22px;
    }

    &:nth-child(4) {
      width: 435px;
    }

    &:nth-child(6) {
      margin-bottom: 0;
    }
  }
}

.contest__submit {
  width: 100%;
  max-width: 300px;
  margin-bottom: 35px;

  @media (min-width: @tablet-width) {
    margin-bottom: 0;
  }
}
