// * effect

.effect {
  display: flex;
  flex-wrap: wrap;

  @media (min-width: @tablet-width) {
    align-items: center;
    justify-content: space-between;
  }
}

.effect__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  .mp-reset();
  border: none;
  background-color: transparent;
  cursor: pointer;

  @media (min-width: @tablet-width) {
    width: 24px;
    height: 24px;
  }

  @media (min-width: @desktop-width) {
    width: 50px;
    height: 45px;
  }
}

.effect__icon {
  fill: @page-dark-gray;

  object-fit: contain;

  @media (min-width: @tablet-width) {
    fill: @page-red;
  }
}

.effect__icon--crop {
  @media (min-width: @tablet-width) {
    width: 22px;
    height: 22px;
  }

  @media (min-width: @desktop-width) {
    width: 44px;
    height: 44px;
  }
}

.effect__icon--fill {
  @media (min-width: @tablet-width) {
    width: 24px;
    height: 22px;
  }

  @media (min-width: @desktop-width) {
    width: 48px;
    height: 43px;
  }
}

.effect__icon--contrast {
  @media (min-width: @tablet-width) {
    width: 16px;
    height: 16px;
  }

  @media (min-width: @desktop-width) {
    width: 32px;
    height: 32px;
  }
}

.effect__button--active .effect__icon {
  fill: @page-red;
}

// * slider

.effect__slider {
  display: none;
  align-items: center;
  width: 100%;
  height: 14px;

  @media (min-width: @tablet-width) {
    display: flex;
    width: 79%;
  }

  @media (min-width: @desktop-width) {
    width: 288px;
  }
}

.effect__slider-bar {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: @basic-black;
}

.effect__slider-control {
  position: absolute;
  top: -6px;
  left: 50%;
  width: 14px;
  height: 14px;
  border: 2px solid @page-red;
  border-radius: 50%;
  background-color: @basic-white;
  cursor: w-resize;
}
