// hero

.hero__link {
  position: relative;
  display: block;

  &::before {
    content: attr(data-hero);
    position: absolute;
    z-index: 505;
    top: 0;
    right: 20px;
    width: 140px;
    min-height: 36px;
    padding-top: 6px;
    font-size: 12px;
    line-height: 24px;
    font-weight: 700;
    color: @basic-white;
    text-transform: uppercase;
    text-align: center;
    background-color: @page-red;
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: 57px;
      width: 21.2%;
      padding-top: 7px;
    }
  }

  @media (min-width: 1200px) {
    width: 1200px;
    .centered();

    &::before {
      top: 54px;
      right: 130px;
      width: 140px;
      height: 36px;
    }
  }
}

.hero__img {
  width: 100%;
}

.hero__content {
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    .card {
      width: 100%;
    }

    .card__content {
      display: flex;
      flex-wrap: wrap;
      flex-direction: row;
      align-items: center;
      justify-content: space-between;
      min-height: 117px;
      padding-top: 24px;
      padding-right: 24px;
      padding-bottom: 26px;
    }

    .card__comment {
      order: 1;
      margin-bottom: 0;
    }

    .card__like {
      padding-right: 35px;
    }
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
    padding-right: 10px;
    padding-left: 10px;

    .card__content {
      min-height: auto;
      padding-top: 24px;
      padding-right: 32px;
      padding-bottom: 25px;
      padding-left: 28px;
    }

    .card__comment {
      min-height: auto;
    }

    .card__like {
      padding-right: 30px;
    }
  }
}
