// card
.card__img {
  width: 100%;

  @media (min-width: @tablet-width) {
    width: 100%;
  }
}

.card__content {
  padding-top: 18px;
  padding-right: 20px;
  padding-bottom: 19px;
  padding-left: 17px;
  background-color: @page-gray;

  @media (min-width: @tablet-width) {
    min-height: 155px;
    padding-top: 24px;
    padding-right: 28px;
    padding-left: 27px;
    padding-bottom: 21px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 24px;
    padding-right: 32px;
  }
}

.card__author {
  font-size: 1em;
  font-weight: 700;
  font-style: normal;
}

.card__time {
  font-size: 0.86em;
  font-weight: 300;
  text-transform: uppercase;
}

.card__comment {
  margin-bottom: 9px;

  @media (min-width: @tablet-width) {
    min-height: 46px;
    margin-bottom: 17px;
  }
}

.card__like {
  position: relative;
  padding-left: 32px;
  font-size: 0.86em;
  color: @page-red;
  text-transform: uppercase;
  text-decoration: none;

  &::before {
    content: "";
    position: absolute;
    top: 3px;
    left: 0;
    width: 13px;
    height: 13px;
    background-image: url("../img/icon-like.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: center;
  }

  @media (min-width: @tablet-width) {
    display: block;
    padding-left: 0;
    padding-right: 32px;

    &::before {
      left: auto;
      right: 5px;
    }
  }

  @media (min-width: @desktop-width) {
    padding-right: 30px;

    &::before {
      right: 0;
    }
  }

  // states

  &:hover,
  &:hover::before {
    opacity: 0.7;
  }

  &:active,
  &:active::before {
    opacity: 0.3;
  }
}
