// upload-app
.upload-app {
  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
    margin-bottom: 25px;
    padding-right: 10px;
    padding-left: 10px;
  }
}

.upload-app__title {
  padding-top: 23px;
  padding-right: 20px;
  padding-left: 16px;
  padding-bottom: 42px;
  font-size: 0.875em;
  line-height: 2.15;
  text-align: center;

  @media (min-width: @tablet-width) {
    padding-top: 47px;
    padding-bottom: 70px;
    font-size: 1.125em;
    line-height: 1.66;
  }

  @media (min-width: @desktop-width) {
    padding-top: 5px;
    padding-right: 10px;
    padding-bottom: 66px;
    padding-left: 10px;
  }
}

.upload-app__form {
  padding-top: 20px;
  padding-right: 22px;
  padding-bottom: 27px;
  padding-left: 18px;
  background-color: @page-gray;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-top: 56px;
    padding-bottom: 32px;
    padding-left: 20px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 57px;
    padding-right: 60px;
    padding-left: 80px;
    padding-bottom: 58px;
  }
}

// * upload

.upload-app__upload {
  margin-bottom: 40px;

  @media (min-width: @tablet-width) {
    width: 48.5%;
    margin-bottom: 0;
  }

  @media (min-width: @desktop-width) {
    width: 300px;
  }
}

// * controls

.upload-app__controls {
  @media (min-width: @tablet-width) {
    width: 44%;
  }

  @media (min-width: @desktop-width) {
    width: 410px;
  }
}

.upload-app__effects {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  .mp-reset();
  margin-bottom: 37px;
  padding-right: 20px;
  padding-bottom: 28px;
  padding-left: 20px;
  border-bottom: 1px solid @basic-light;
  list-style: none;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    width: auto;
    margin-bottom: 49px;
    padding-top: 1px;
    padding-right: 18px;
    padding-left: 0;
    padding-bottom: 0;
    border-bottom: none;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 135px;
    padding-top: 0;
    padding-right: 30px;
  }
}

// * effects

.upload-app__effect {
  margin-bottom: 31px;

  @media (min-width: @tablet-width) {
    margin-bottom: 17px;
    padding-bottom: 16px;
    border-bottom: 1px solid @basic-light;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 13px;
    padding-bottom: 11px;
  }
}

.upload-app__effect--m-only {
  width: 100%;
  margin: 0;

  .effect__button,
  .effect__icon {
    display: none;
  }

  .effect__slider {
    display: flex;
  }

  @media (min-width: @tablet-width) {
    margin: 0;
    padding: 0;
    border-bottom: none;

    .effect__slider {
      display: none;
    }
  }

  @media (min-width: @desktop-width) {
    margin: 0;
    padding: 0;
    border-bottom: none;
  }
}

.upload-app__effect-slider--m-only {
  display: flex;

  @media (min-width: @tablet-width) {
    display: none;
  }
}

// * buttons area

.upload-app__submit {
  @media (min-width: @tablet-width) {
    padding-right: 12px;
  }

  @media (min-width: @desktop-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding-right: 0;
  }
}

.upload-app__button {
  width: 100%;
  margin-bottom: 14px;
  color: @page-red;
  line-height: 1;
  border-color: @page-red;
  border-radius: 35px;

  &:hover {
    color: @basic-white;
    background-color: @page-red;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @tablet-width) {
    margin-bottom: 24px;
  }

  @media (min-width: @desktop-width) {
    width: 183px;
    font-size: 18px;
    margin-bottom: 0;
  }
}

.upload-app__button--reset {
  color: @page-dark-gray;
  border-color: @page-dark-gray;
  opacity: 0.3;

  &:hover {
    color: @page-dark-gray;
    background-color: transparent;
    border-color: @page-dark-gray;
    opacity: 1;
  }

  &:active {
    color: @basic-light;
    background-color: @page-dark-gray;
  }
}
