// * upload-photo

.upload-photo {
  position: relative;
  overflow: hidden;
}

.upload-photo__input {
  // ! additional vh class props
  position: absolute;
  z-index: -1;
  // ! prevent unwanted scroll
  top: 50%;
  opacity: 0;
  overflow: hidden;
}

.upload-photo__mask {
  display: block;
  width: 100%;
  background-color: @basic-light;
}

.upload-photo__preview {
  width: 100%;
  cursor: pointer;
  transition-property: transform, filter;
  transition-duration: 0.15s;
  transition-timing-function: linear;
}

// * effect states

.upload-photo__preview[data-effect="scale"] {
  transform: scale(1);
}

.upload-photo__preview[data-effect="grayscale"] {
  filter: grayscale(1);
}

.upload-photo__preview[data-effect="contrast"] {
  filter: contrast(4);
}

// * upload states

.upload-photo__input + .upload-photo__mask {
  cursor: pointer;
}

.upload-photo__input:focus + .upload-photo__mask {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: 3px solid @page-red;
  }
}
