// fact

.fact {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    font-size: 0.875em;
    line-height: 1.71;
  }

  @media (min-width: @desktop-width) {
    background-color: @page-gray;
  }
}

.fact__content {
  position: relative;
  padding-top: 96px;
  padding-right: 20px;
  padding-bottom: 60px;
  padding-left: 20px;

  &::before {
    content: "";
    position: absolute;
    top: 57px;
    left: 22px;
    width: 238px;
    height: 276px;
    background-image: url("../img/icon-timer.svg");
    background-repeat: no-repeat;
    background-size: 100%;
    background-position: 0 0;
  }

  &::after {
    content: "24";
    position: absolute;
    top: 133px;
    left: 85px;
    font-size: 96px;
    font-weight: 700;
    line-height: 149px;
    color: @page-red;
  }

  @media (min-width: @tablet-width) {
    padding-top: 93px;
    padding-bottom: 60px;
  }

  @media (min-width: @desktop-width) {
    .centered();
    width: 960px;
    padding-top: 104px;
    padding-right: 10px;
    padding-bottom: 112px;
    padding-left: 10px;

    &::before {
      top: 13px;
      left: 10px;
      width: 298px;
      height: 345px;
    }

    &::after {
      top: 110px;
      left: 90px;
      font-size: 120px;
      line-height: 186px;
    }
  }
}

.fact__wrapper {
  width: 50%;
  margin-left: auto;

  @media (min-width: @desktop-width) {
    width: 574px;
  }
}

.fact__title {
  margin-bottom: 11px;
  font-size: 2.143em;
  line-height: 1.2;

  @media (min-width: @tablet-width) {
    width: 59%;
    min-height: 72px;
  }

  @media (min-width: @desktop-width) {
    width: auto;
    min-height: auto;
    margin-bottom: 21px;
  }
}

.fact__desc {
  min-height: 92px;
  margin-bottom: 14px;

  @media (min-width: @tablet-width) {
    margin-bottom: 10px;
    line-height: 1.78;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 29px;
    font-size: 1.286em;
    line-height: 1.72;
  }
}

.fact__more-link {
  color: @page-red;

  &:hover {
    color: @basic-black;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: @desktop-width) {
    font-size: 1.286em;
  }
}
