// carousel

.carousel {
  position: relative;
}

.carousel__content {
  min-height: 250px;

  @media (min-width: @desktop-width) {
    .centered();
    width: 80%;
  }
}

.carousel__controls {
  display: none;
  position: absolute;
  flex-wrap: wrap;
  justify-content: space-between;
  width: 100%;
  height: 42px;

  @media (min-width: @desktop-width) {
    top: 28px;
    left: 0;
    display: flex;
  }
}

.carousel__dots {
  bottom: -23px;
  left: calc(50% - 58px / 2);

  @media (min-width: @tablet-width) {
    bottom: 18px;
  }
}
