// triangled bg

.tri-section {
  &::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 15%;
    background-image: linear-gradient(356deg, @page-gray 50%, @basic-white 50%);
  }

  @media (min-width: @tablet-width) {
    &::before {
      height: 25%;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      height: 50%;
    }
  }
}
