// promo

.promo {
  padding-top: 72px;
  background-color: @page-blue;
  background-image: url("../img/header-bg-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 700px 462px;
  word-break: break-all;

  @media (min-width: @tablet-width) {
    padding-top: 109px;
    background-image: url("../img/header-bg-tablet@1x.jpg");
    background-size: 1200px 729px;
  }

  @media (min-width: @desktop-width) {
    background-image: url("../img/header-bg-desktop@1x.jpg");
    background-size: 2560px 853px;
  }
}

.promo__content {
  &::before {
    content: "";
    position: relative;
    display: block;
    width: 100%;
    height: 395px;
    background-image: url("../img/promo-bg-phone-mobile.png");
    background-repeat: no-repeat;
    background-size: 320px 314px;
    background-position: center 109px;
  }

  @media (min-width: @tablet-width) {
    position: relative;
    min-height: 618px;

    &::before {
      position: absolute;
      left: 0;
      bottom: -4px;
      width: 100%;
      height: 660px;
      background-image: url("../img/promo-bg-phone-tablet.png");
      background-size: 660px 545px;
    }
  }

  @media (min-width: @desktop-width) {
    .centered();
    width: 960px;
    min-height: 743px;
    padding-right: 10px;
    padding-left: 10px;

    &::before {
      bottom: 0;
      height: 743px;
      background-image: url("../img/promo-bg-phone-desktop.png");
      background-size: 1050px 645px;
      background-position: 26px 100px;
    }
  }
}

.promo__wrapper {
  padding-top: 28px;
  padding-right: 20px;
  padding-bottom: 32px;
  padding-left: 20px;
  background-color: @page-blue;

  @media (min-width: @tablet-width) {
    position: relative;
    z-index: 505;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    width: 51.2%;
    padding-top: 90px;
    padding-right: 20px;
    padding-bottom: 0;
    padding-left: 20px;
    background-color: transparent;
  }

  @media (min-width: @desktop-width) {
    width: 300px;
    padding: 0;
    padding-top: 109px;
  }
}

.promo__title {
  display: none;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    display: block;
    width: 66%;
    margin-bottom: 32px;
    line-height: 1.2;
    text-align: center;
  }

  @media (min-width: @desktop-width) {
    width: 85%;
    margin-bottom: 50px;
  }
}

.promo__button {
  margin-bottom: 33px;

  @media (min-width: @tablet-width) {
    min-width: auto;
    width: 100%;
    margin-bottom: 47px;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 65px;
  }
}

.promo__platform {
  margin-bottom: 23px;
  padding-right: 31px;
  padding-left: 31px;

  @media (min-width: @tablet-width) {
    width: 75%;
    margin-bottom: 38px;
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 58px;
  }
}

.promo__desc {
  width: 83%;
  min-height: 46px;
  .centered();
  font-size: 0.875em;
  line-height: 1.71;
  font-weight: 300;
  color: @basic-white;
  text-align: center;

  @media (min-width: @tablet-width) {
    width: auto;
    font-size: 1.125em;
    line-height: 1.3;
  }
}

.promo__content--inner {
  min-height: 170px;
  padding-top: 29px;
  padding-right: 20px;
  padding-left: 20px;
  padding-bottom: 30px;

  &::before {
    display: none;
  }

  @media (min-width: @tablet-width) {
    min-height: 330px;
  }

  @media (min-width: @desktop-width) {
    min-height: 285px;
  }

  .promo__title {
    display: block;
    .centered();
    font-size: 1.5em;
    font-weight: 300;
    line-height: 1.23;
    text-align: center;
    word-break: normal;

    @media (min-width: @tablet-width) {
      margin-top: 39px;
      font-size: 3.125em;
    }

    @media (min-width: @desktop-width) {
      width: 65%;
      margin-top: 35px;
    }
  }

  .promo__title--form {
    @media (min-width: @tablet-width) {
      width: 100%;
      line-height: 1.36;
    }

    @media (min-width: @desktop-width) {
      width: 100%;
      line-height: 1.3;
    }
  }
}

// states
.promo--active {
  padding-top: 0;
  background-position: center -70px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
    background-position: center -112px;
  }
}
