// photo

.photo {
  font-size: 0.875em;
  line-height: 1.72;
}

.photo__header {
  line-height: 2.2;

  @media (min-width: @tablet-width) {
    padding-right: 70px;
    padding-left: 70px;
    font-size: 1.286em;
    line-height: 1.66;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    min-height: 240px;
    padding-right: 160px;
    padding-left: 160px;
    .centered();
  }
}

.photo__hero {
  width: 100%;

  .card {
    margin-bottom: 22px;
  }

  @media (min-width: @tablet-width) {
    .card {
      margin-bottom: 49px;
    }
  }

  @media (min-width: 1200px) {
    width: 1200px;
    .centered();
  }
}

.photo__content {
  .mp-reset();
  list-style: none;
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    justify-content: flex-start;
    .centered();
    padding-right: 10px;
    padding-left: 10px;
  }
}

.photo__card {
  margin-bottom: 22px;

  @media (min-width: @tablet-width) {
    width: 48.4%;
    margin-bottom: 20px;
  }

  @media (min-width: @desktop-width) {
    width: 300px;
    margin-bottom: 55px;
    margin-right: 20px;

    &:nth-child(3n) {
      margin-right: 0;
    }
  }
}
