// personal-app-info

.app-info {
  position: relative;
  padding-top: 117px;
  background-color: @page-gray;

  @media (min-width: @tablet-width) {
    position: static;
    padding-top: 0;
    background-color: transparent;
    border: 2px solid @page-gray;
    border-radius: 2px;
  }
}

.app-info__title {
  position: absolute;
  top: 41px;
  left: 50%;
  width: 90%;
  transform: translateX(-50%);
  text-align: center;

  @media (min-width: @tablet-width) {
    position: static;
    top: auto;
    left: auto;
    width: 366px;
    transform: none;
  }
}

.app-info__content {
  @media (min-width: @tablet-width) {
    padding-top: 46px;
    padding-right: 49px;
    padding-bottom: 50px;
    padding-left: 49px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 44px;
    padding-right: 60px;
    padding-left: 64px;
  }
}

.app-info__group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 38px;

  @media (min-width: @tablet-width) {
    &:last-child {
      margin-bottom: 0;
    }
  }
}

.app-info__input:checked ~ .app-info__label::after {
  display: block;
}
