// feedback

.feedback {
  position: relative;
  padding-top: 72px;

  @media (min-width: @tablet-width) {
    padding-top: 0;
    border: 2px solid @page-gray;
  }
}

.feedback__title {
  position: absolute;
  top: 38px;
  left: 50%;
  width: 100%;
  text-align: center;
  transform: translateX(-50%);

  @media (min-width: @tablet-width) {
    position: static;
    top: auto;
    left: auto;
    width: 260px;
    transform: translateX(0);
  }
}

.feedback__content {
  padding-bottom: 35px;

  @media (min-width: @tablet-width) {
    padding-top: 32px;
    padding-right: 58px;
    padding-bottom: 49px;
    padding-left: 58px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 27px;
    padding-bottom: 40px;
  }
}

.feedback__text-area {
  width: 100%;
  min-height: 200px;
  resize: none;

  @media (min-width: @tablet-width) {
    padding-right: 20px;
    min-height: 192px;
  }

  @media (min-width: @desktop-width) {
    min-height: 128px;
  }
}
