// arrow

.arrow {
  position: relative;
  width: 22px;
  height: 41px;
  .mp-reset();
  border: none;
  background-color: transparent;
  cursor: pointer;

  &::before,
  &::after {
    content: "";
    position: absolute;
    top: -4px;
    right: 11px;
    width: 2px;
    height: 70%;
    background-color: @basic-neutral;
    transform: rotate(45deg);
    transition-property: opacity;
    transition-duration: 0.2s;
    transition-timing-function: linear;
  }

  &::after {
    top: auto;
    bottom: -5px;
    transform: rotate(-45deg);
  }

  &:hover::before,
  &:hover::after {
    opacity: 0.8;
  }

  &:active::before,
  &:active::after {
    opacity: 0.3;
  }
}

.arrow--next {
  transform: rotate(180deg);
}
