@font-face {
  font-family: "OpenSans";
  font-display: swap;
  font-style: normal;
  font-weight: 300;
  src: local("OpenSans-Light"), url("../fonts/OpenSans-Light.woff2") format("woff2"), local("Open Sans Light"), url("../fonts/OpenSans-Light.woff") format("woff");
}

@font-face {
  font-family: "OpenSans";
  font-display: swap;
  font-style: normal;
  font-weight: 400;
  src: local("OpenSans-Regular"), url("../fonts/OpenSans-Regular.woff2") format("woff2"), local("Open Sans Regular"), url("../fonts/OpenSans-Regular.woff") format("woff");
}

@font-face {
  font-family: "OpenSans";
  font-display: swap;
  font-style: normal;
  font-weight: 700;
  src: local("OpenSans-Bold"), url("../fonts/OpenSans-Bold.woff2") format("woff2"), local("Open Sans Bold"), url("../fonts/OpenSans-Bold.woff") format("woff");
}

/* Указываем box sizing */

*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Убираем внешние отступы */

body,
h1,
h2,
h3,
h4,
p,
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}

html {
  scroll-behavior: smooth;
}

/* Выставляем основные настройки по-умолчанию для body */

body {
  min-width: 320px;
  min-height: 100vh;
  overflow-y: scroll;
  font-family: "OpenSans", "Arial", sans-serif;
  color: #000000;
  overflow-wrap: break-word;
}

/* Упрощаем работу с изображениями */

img {
  max-width: 100%;
  height: auto;
  display: block;
}

input,
button,
textarea,
select {
  font: inherit;
}

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.navigation {
  display: none;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: #283645;
}

.navigation a {
  width: 100%;
  padding-top: 13px;
  padding-bottom: 20px;
  color: #ffffff;
  font-weight: 700;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
  border-top: 1px solid #000000;
  box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1);
}

.navigation a:hover {
  color: #d22856;
}

.navigation a:active {
  opacity: 0.3;
}

.navigation a:last-child {
  padding-bottom: 17px;
  border-bottom: 1px solid #000000;
}

.header--active .navigation {
  display: flex;
}

.header--nojs .navigation {
  display: flex;
}

.platform {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
}

.platform__link {
  display: block;
  width: 56px;
  height: 56px;
  background-repeat: no-repeat;
  background-position: center;
}

.platform__link--apple {
  background-image: url("../img/promo-apple-icon.svg");
  background-size: 47px 56px;
}

.platform__link--android {
  background-image: url("../img/promo-android-icon.svg");
  background-size: 43px 47px;
  background-position: 50% 8px;
}

.platform__link--windows {
  background-image: url("../img/promo-windows-icon.svg");
  background-size: 44px 41px;
  background-position: center 12px;
}

.button {
  display: block;
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-right: 5px;
  padding-bottom: 17px;
  padding-left: 5px;
  font-weight: 700;
  font-size: 18px;
  line-height: 1;
  color: #ffffff;
  text-decoration: none;
  text-align: center;
  background-color: transparent;
  border: 2px solid #ffffff;
  border-radius: 25px;
}

.button:hover {
  background-color: #ffffff;
  color: #d22856;
}

.button:active {
  color: #f1becc;
}

.title {
  font-size: 1.5em;
  line-height: 1.2;
  font-weight: 300;
}

.arrow {
  position: relative;
  width: 22px;
  height: 41px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.arrow::before,
.arrow::after {
  content: "";
  position: absolute;
  top: -4px;
  right: 11px;
  width: 2px;
  height: 70%;
  background-color: #b1b3b6;
  transform: rotate(45deg);
  transition-property: opacity;
  transition-duration: 0.2s;
  transition-timing-function: linear;
}

.arrow::after {
  top: auto;
  bottom: -5px;
  transform: rotate(-45deg);
}

.arrow:hover::before,
.arrow:hover::after {
  opacity: 0.8;
}

.arrow:active::before,
.arrow:active::after {
  opacity: 0.3;
}

.arrow--next {
  transform: rotate(180deg);
}

.dots {
  position: absolute;
  z-index: 505;
  width: 58px;
  min-height: 14px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
}

.dots__dot {
  position: relative;
  padding: 0;
  width: 6px;
  height: 6px;
  border: none;
  border-radius: 50%;
  background-color: #b1b3b6;
  cursor: pointer;
}

.dots__dot::before {
  content: "";
  position: absolute;
  top: -5px;
  left: -5px;
  width: 15px;
  height: 15px;
  background-color: transparent;
}

.dots__dot--active {
  position: relative;
  background-color: #ffffff;
}

.dots__dot--active::before {
  content: "";
  position: absolute;
  top: -2px;
  left: -2px;
  width: 10px;
  height: 10px;
  border: 2px solid #000000;
  border-radius: 50%;
}

.tri-section::before {
  content: "";
  position: absolute;
  left: 0;
  width: 100%;
  height: 15%;
  background-image: linear-gradient(356deg, #f2f2f2 50%, #ffffff 50%);
}

.effect {
  display: flex;
  flex-wrap: wrap;
}

.effect__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 48px;
  height: 48px;
  margin: 0;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.effect__icon {
  fill: #b5b9be;
  object-fit: contain;
}

.effect__button--active .effect__icon {
  fill: #d22856;
}

.effect__slider {
  display: none;
  align-items: center;
  width: 100%;
  height: 14px;
}

.effect__slider-bar {
  position: relative;
  width: 100%;
  height: 2px;
  background-color: #000000;
}

.effect__slider-control {
  position: absolute;
  top: -6px;
  left: 50%;
  width: 14px;
  height: 14px;
  border: 2px solid #d22856;
  border-radius: 50%;
  background-color: #ffffff;
  cursor: w-resize;
}

.inner-head {
  min-height: 184px;
  padding-top: 45px;
  padding-right: 35px;
  padding-left: 35px;
  padding-bottom: 35px;
  text-align: center;
}

.pf__fld {
  margin: 0;
  padding: 0;
  border: none;
}

.pf__cnt {
  padding-right: 20px;
  padding-left: 20px;
}

.pf__ld {
  font-size: 0.875em;
  line-height: 1.28;
  color: #d22856;
  font-weight: 400;
  text-transform: uppercase;
}

.pf__txt {
  font-size: 1.125em;
  line-height: 1.666;
  font-weight: 400;
}

.pf__bdr {
  border: 2px solid #f2f2f2;
  border-radius: 2px;
}

.pf__ipt {
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 15px;
  padding-left: 20px;
  border: 2px solid #f2f2f2;
  border-radius: 2px;
}

.pf__lbl-chk {
  position: relative;
  min-height: 35px;
  padding-left: 59px;
  cursor: pointer;
  user-select: none;
}

.pf__lbl-chk::before {
  content: "";
  position: absolute;
  top: -4px;
  left: 0;
  width: 38px;
  height: 38px;
  border: 2px solid #d7dcde;
  background-color: #ffffff;
}

.pf__lbl-chk:hover::before {
  border-color: #000000;
}

.pf__lbl-chk:active::before {
  border-color: #d22856;
}

.pf__lbl-chk::after {
  content: "";
  position: absolute;
  display: none;
  top: 6px;
  left: 9px;
  width: 22px;
  height: 16px;
  background-image: url("../img/icon-achiev-tick.svg");
  background-repeat: no-repeat;
  background-size: 22px 16px;
  background-position: 0 0;
}

.pf__lbl-rdo {
  position: relative;
  min-height: 37px;
  padding-left: 70px;
  cursor: pointer;
  user-select: none;
}

.pf__lbl-rdo::before {
  content: "";
  position: absolute;
  top: -5px;
  left: 0;
  width: 40px;
  height: 40px;
  border: 2px solid #d7dcde;
  border-radius: 100%;
  background-color: #ffffff;
}

.pf__lbl-rdo::after {
  content: "";
  position: absolute;
  top: 8px;
  left: 12px;
  display: none;
  width: 15px;
  height: 15px;
  background-color: #d22856;
  border-radius: 100%;
}

.pf__lbl-rdo:hover::before {
  border-color: #000000;
}

.pf__lbl-rdo:active::before {
  border-color: #d22856;
}

.pf__txt-a {
  width: 100%;
  min-height: 197px;
}

.pf__btn {
  margin: 0;
  padding: 0;
  padding-top: 15px;
  padding-right: 25px;
  padding-bottom: 15px;
  padding-left: 25px;
  font-size: 1.125em;
  line-height: 1.66;
  color: #ffffff;
  text-transform: uppercase;
  border: 2px solid #2aab6d;
  border-radius: 2px;
  background-color: #2aab6d;
}

.pf__ipt--h:hover {
  border-color: #000000;
}

.pf__ipt--h:active {
  border-color: #d22856;
}

.header {
  position: absolute;
  z-index: 506;
  width: 100%;
  font-size: 1.125em;
  line-height: 1.7;
  color: #ffffff;
  background-color: rgba(0, 0, 0, 0.3);
  word-break: break-all;
}

.header__content {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  margin-left: auto;
  margin-right: auto;
}

.header__head {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  min-width: 320px;
  width: 100%;
  padding-right: 20px;
  padding-top: 21px;
  padding-bottom: 26px;
  padding-left: 20px;
}

.header__navigation {
  width: 100%;
}

.header--active,
.header--nojs {
  position: static;
  background-color: #283645;
}

.toggle-nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 25px;
  height: 25px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.toggle-nav:hover .toggle-nav__icon {
  fill: #d22856;
}

.toggle-nav:active .toggle-nav__icon {
  fill: #d22856;
  opacity: 0.3;
}

.toggle-nav__icon {
  fill: #ffffff;
}

.toggle-nav__icon-menu {
  display: block;
}

.toggle-nav__icon-cross {
  display: none;
}

.header--active .toggle-nav__icon-menu {
  display: none;
}

.header--active .toggle-nav__icon-cross {
  display: block;
}

.promo {
  padding-top: 72px;
  background-color: #283645;
  background-image: url("../img/header-bg-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: 700px 462px;
  word-break: break-all;
}

.promo__content::before {
  content: "";
  position: relative;
  display: block;
  width: 100%;
  height: 395px;
  background-image: url("../img/promo-bg-phone-mobile.png");
  background-repeat: no-repeat;
  background-size: 320px 314px;
  background-position: center 109px;
}

.promo__wrapper {
  padding-top: 28px;
  padding-right: 20px;
  padding-bottom: 32px;
  padding-left: 20px;
  background-color: #283645;
}

.promo__title {
  display: none;
  color: #ffffff;
}

.promo__button {
  margin-bottom: 33px;
}

.promo__platform {
  margin-bottom: 23px;
  padding-right: 31px;
