}

.slogan__separator {
  display: block;
  width: 100%;
  margin-bottom: 30px;
}

.features {
  position: relative;
  font-size: 0.875em;
  line-height: 1.72;
  background-color: #f2f2f2;
}

.features::before {
  top: -66px;
}

.features__content {
