}

.feature::before {
  content: "";
  position: absolute;
  top: 0;
  left: calc(50% - 187px / 2);
  display: none;
  width: 187px;
  height: 300px;
  background-image: url("../img/features-icon-mood.png");
  background-repeat: no-repeat;
