}

.fact__content {
  position: relative;
  padding-top: 96px;
  padding-right: 20px;
  padding-bottom: 60px;
  padding-left: 20px;
}

.fact__content::before {
  content: "";
  position: absolute;
  top: 57px;
  left: 22px;
  width: 238px;
  height: 276px;
  background-image: url("../img/icon-timer.svg");
  background-repeat: no-repeat;
  background-size: 100%;
  background-position: 0 0;
}

.fact__content::after {
  content: "24";
  position: absolute;
  top: 133px;
  left: 85px;
  font-size: 96px;
  font-weight: 700;
  line-height: 149px;
