// feature

.feature {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: calc(50% - 187px / 2);
    display: none;
    width: 187px;
    height: 300px;
    background-image: url("../img/features-icon-mood.png");
    background-repeat: no-repeat;
    background-size: 187px 381px;
    background-position: 0 0;
  }

  @media (min-width: @desktop-width) {
    position: relative;
    padding-top: 360px;
    padding-right: 27px;
    padding-bottom: 10px;
    padding-left: 27px;
    text-align: center;

    &::before {
      display: block;
      top: 3px;
    }

    &--world {
      &::before {
        background-image: url("../img/features-icon-world.png");
      }
    }

    &--friends {
      &::before {
        background-image: url("../img/features-icon-3.png");
      }
    }
  }
}

.feature__title {
  margin-bottom: 12px;
  font-size: 1.286em;
  line-height: 1.33;

  @media (min-width: @desktop-width) {
    margin-bottom: 24px;
    font-size: 2.143em;
    line-height: 1;
  }
}

.feature__desc {
  @media (min-width: @desktop-width) {
    font-size: 1.286em;
    line-height: 1.7;
  }
}
