// platform.less

.platform {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  .mp-reset();
  list-style: none;
}

.platform__link {
  display: block;
  width: 56px;
  height: 56px;
  background-repeat: no-repeat;
  background-position: center;
}

.platform__link--apple {
  background-image: url("../img/promo-apple-icon.svg");
  background-size: 47px 56px;
}

.platform__link--android {
  background-image: url("../img/promo-android-icon.svg");
  background-size: 43px 47px;
  background-position: 50% 8px;
}

.platform__link--windows {
  background-image: url("../img/promo-windows-icon.svg");
  background-size: 44px 41px;
  background-position: center 12px;
}
