// section-head

.inner-head {
  min-height: 184px;
  padding-top: 45px;
  padding-right: 35px;
  padding-left: 35px;
  padding-bottom: 35px;
  text-align: center;

  @media (min-width: @tablet-width) {
    min-height: 225px;
    padding-top: 90px;
    padding-bottom: 70px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 93px;
    padding-right: 10px;
    padding-bottom: 80px;
    padding-left: 10px;
  }
}
