// plan table

.plan {
  text-transform: uppercase;
}

.plan__head--empty {
  display: none;
  color: transparent;
  border: none;

  @media (min-width: @tablet-width) {
    display: table-cell;
  }
}

.plan__wrapper {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  padding-top: 15px;
  padding-bottom: 20px;
  background-color: @page-blue;
  color: @basic-white;

  @media (min-width: @tablet-width) {
    padding-top: 13px;
    padding-bottom: 7px;
  }

  @media (min-width: @desktop-width) {
    padding-top: 17px;
    padding-bottom: 22px;
  }
}

.popular {
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 74px;
    height: 74px;
    background-image: url("../img/table-ribbon.png");
    background-size: 100%;
    background-repeat: no-repeat;
    background-position: center;
  }

  @media (min-width: @tablet-width) {
    &::before {
      width: 61px;
      height: 61px;
    }
  }
}

.plan__title {
  font-size: 1.286em;
  line-height: 1.66;

  @media (min-width: @tablet-width) {
    font-size: 1.143em;
    line-height: 1.87;
  }

  @media (min-width: @desktop-width) {
    font-size: 1.286em;
    line-height: 1.66;
  }
}

.plan__price {
  font-weight: 300;
  font-size: 1.714em;
  line-height: 1.4;

  @media (min-width: @tablet-width) {
    font-size: 1.286em;
    line-height: 1.66;
  }

  @media (min-width: @desktop-width) {
    font-size: 1.714em;
    line-height: 1.25;
  }
}

.plan__data {
  padding-top: 15px;
  padding-right: 15px;
  padding-bottom: 15px;
  padding-left: 22px;
  font-weight: 400;
  font-size: 1em;
  line-height: 2.14;
  background-color: @basic-white;

  &:first-child {
    display: none;
  }

  @media (min-width: @tablet-width) {
    display: table-cell;
    padding-top: 7px;
    padding-right: 8px;
    padding-bottom: 9px;
    padding-left: 15px;
    text-transform: capitalize;

    &:first-child {
      display: table-cell;
    }
  }

  @media (min-width: @desktop-width) {
    padding-top: 15px;
    padding-right: 22px;
    padding-bottom: 14px;
    padding-left: 22px;
    font-size: 1.286em;
    line-height: 1.66;
  }
}

.plan__data--true,
.plan__data--false {
  position: relative;

  &::before {
    content: "";
    position: absolute;
    top: 19px;
    right: 18px;
    width: 31px;
    height: 21px;
    background-image: url("../img/icon-table-true.svg");
    background-repeat: no-repeat;
    background-size: 31px 21px;
    background-position: 0 0;
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: 50%;
      right: auto;
      left: 50%;
      transform: translate(-50%, -50%);
    }

    span {
      color: transparent;
    }
  }
}

.plan__data--false {
  &::before {
    top: 15px;
    right: 19px;
    width: 27px;
    height: 26px;
    background-image: url("../img/icon-table-false.svg");
    background-size: 27px 26px;
  }
}

// states

.plan-active {
  display: table-cell;
}
