// offer

.offer {
  position: relative;
  font-size: 0.875em;
  line-height: 2.14;
  background-color: @page-gray;

  &::before {
    top: -35px;
  }

  @media (min-width: @tablet-width) {
    &::before {
      top: -48px;
    }
  }

  @media (min-width: @desktop-width) {
    &::before {
      top: -134px;
    }
  }
}

.offer__content {
  position: relative;
  padding-top: 75px;
  padding-right: 20px;
  padding-bottom: 100px;
  padding-left: 20px;
  overflow: hidden;

  @media (min-width: @tablet-width) {
    padding-top: 55px;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 68px;
  }

  @media (min-width: @desktop-width) {
    width: 960px;
    .centered();
    padding-top: 91px;
    padding-right: 10px;
    padding-left: 10px;
    padding-bottom: 95px;
  }
}

.offer__plan {
  // width: 300%;
  width: 840px;
  // transform: translateX(-280px);
  // transform: translateX(-33.3%);

  @media (min-width: @tablet-width) {
    width: 100%;
    transform: translateX(0);
  }
}

.offer__dots {
  left: 50%;
  bottom: 47px;
  transform: translateX(-50%);

  @media (min-width: @tablet-width) {
    display: none;
  }
}
