// navigation

.navigation {
  display: none;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: @page-blue;

  a {
    width: 100%;
    padding-top: 13px;
    padding-bottom: 20px;
    color: @basic-white;
    font-weight: 700;
    text-align: center;
    text-decoration: none;
    text-transform: uppercase;
    border-top: 1px solid @basic-black;
    box-shadow: 0 0 0 1px rgba(255, 255, 255, 0.1);

    &:hover {
      color: @page-red;
    }

    &:active {
      opacity: 0.3;
    }
  }

  a:last-child {
    padding-bottom: 17px;
    border-bottom: 1px solid @basic-black;
  }

  @media (min-width: @tablet-width) {
    a {
      padding-top: 18px;
      padding-bottom: 15px;
    }
  }

  @media (min-width: @desktop-width) {
    display: flex;
    justify-content: space-between;
    flex-direction: row;

    background-color: transparent;

    a {
      width: auto;
      padding-top: 17px;
      padding-bottom: 14px;
      font-weight: 400;
      line-height: 1;
      text-transform: capitalize;
      box-shadow: none;
      border-top: 0;
    }

    a:last-child {
      border-bottom: none;
    }
  }
}

.header--active .navigation {
  display: flex;
}

.header--nojs .navigation {
  display: flex;
}

.link-active {
  @media (min-width: @desktop-width) {
    border-bottom: 2px solid @basic-white;
  }
}
