// pink-form
// glossary

// pf -> pink-form
// pf__bdr -> pink-form__border
// pf__fld -> pink-form__field
// pf__cnt -> pink-form__content
// pf__ld -> pink-form__lead
// pf__txt -> pink-form__text
// pf__ipt -> pink-form__input
// pf__ipt--h -> pink-form__input--hover
// pf__lbl-chk -> pink-form__label-checkbox
// pf__lbl-rdo -> pink-form__label-radio
// pf__txt-a -> pink-form__text-area
// pf__btn -> pink-form__button

.pf__fld {
  .mp-reset();
  border: none;
}

.pf__cnt {
  padding-right: 20px;
  padding-left: 20px;

  @media (min-width: @tablet-width) {
    padding: 0;
  }
}

.pf__ld {
  font-size: 0.875em;
  line-height: 1.28;
  color: @page-red;
  font-weight: 400;
  text-transform: uppercase;

  @media (min-width: @tablet-width) {
    line-height: 2.14;
  }
}

.pf__txt {
  font-size: 1.125em;
  line-height: 1.666;
  font-weight: 400;
}

.pf__bdr {
  border: 2px solid @page-gray;
  border-radius: 2px;
}

.pf__ipt {
  padding-top: 15px;
  padding-right: 20px;
  padding-bottom: 15px;
  padding-left: 20px;
  border: 2px solid @page-gray;
  border-radius: 2px;

  @media (min-width: @tablet-width) {
    padding-right: 59px;
  }
}

// * custom checkbox

.pf__lbl-chk {
  position: relative;
  min-height: 35px;
  padding-left: 59px;
  cursor: pointer;
  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: -4px;
    left: 0;
    width: 38px;
    height: 38px;
    border: 2px solid @basic-light;
    background-color: @basic-white;
  }

  &:hover::before {
    border-color: @basic-black;
  }

  &:active::before {
    border-color: @page-red;
  }

  &::after {
    content: "";
    position: absolute;
    display: none;
    top: 6px;
    left: 9px;
    width: 22px;
    height: 16px;
    background-image: url("../img/icon-achiev-tick.svg");
    background-repeat: no-repeat;
    background-size: 22px 16px;
    background-position: 0 0;
  }
}

// * custom radio

.pf__lbl-rdo {
  position: relative;
  min-height: 37px;
  padding-left: 70px;
  cursor: pointer;
  user-select: none;

  &::before {
    content: "";
    position: absolute;
    top: -5px;
    left: 0;
    width: 40px;
    height: 40px;
    border: 2px solid @basic-light;
    border-radius: 100%;
    background-color: @basic-white;
  }

  &::after {
    content: "";
    position: absolute;
    top: 8px;
    left: 12px;
    display: none;
    width: 15px;
    height: 15px;
    background-color: @page-red;
    border-radius: 100%;
  }

  &:hover::before {
    border-color: @basic-black;
  }

  &:active::before {
    border-color: @page-red;
  }

  @media (min-width: @tablet-width) {
    &::before {
      left: 9px;
    }

    &::after {
      top: 8px;
      left: 22px;
    }
  }
}

// * textarea

.pf__txt-a {
  width: 100%;
  min-height: 197px;
}

.pf__btn {
  .mp-reset();
  padding-top: 15px;
  padding-right: 25px;
  padding-bottom: 15px;
  padding-left: 25px;
  font-size: 1.125em;
  line-height: 1.66;
  color: @basic-white;
  text-transform: uppercase;
  border: 2px solid #2aab6d;
  border-radius: 2px;
  background-color: @page-green;
}

// * states

.pf__ipt--h {
  &:hover {
    border-color: @basic-black;
  }

  &:active {
    border-color: @page-red;
  }
}
