// comment

.comment {
  position: relative;
  z-index: 505;
  display: none;
  flex-wrap: wrap;
  align-items: center;
  flex-direction: column;
  min-height: 221px;
  text-align: center;
}

.comment__text {
  min-height: 174px;

  @media (min-width: @tablet-width) {
    margin-bottom: 32px;
    min-height: 87px;
    line-height: 1.71;
  }

  @media (min-width: @desktop-width) {
    margin-bottom: 30px;
  }
}

.comment__meta {
  order: -1;
  margin-bottom: 9px;

  @media (min-width: @tablet-width) {
    order: 0;
    margin-bottom: 0;
  }
}

.comment__author {
  font-weight: 700;
  font-style: normal;
  font-size: 1.286em;

  @media (min-width: @tablet-width) {
    font-size: 1.111em;
  }

  @media (min-width: @desktop-width) {
    font-size: 1.333em;
  }
}

.comment__spec {
  display: none;

  @media (min-width: @tablet-width) {
    display: block;
    font-size: 0.778em;
    line-height: 2.14;
  }

  @media (min-width: @desktop-width) {
    font-size: 1em;
    line-height: 1.66;
  }
}

// states

.comment--active {
  display: flex;
}
