// achievments

.achievments {
  position: relative;
  padding-top: 116px;
  background-color: @page-gray;

  @media (min-width: @tablet-width) {
    padding-top: 44px;
    padding-right: 59px;
    padding-bottom: 0;
    padding-left: 58px;
    border: 2px solid @page-gray;
    background-color: transparent;
  }

  @media (min-width: @desktop-width) {
    padding-bottom: 7px;
  }
}

.achievments__title {
  position: absolute;
  top: 38px;
  left: 50%;
  width: 70%;
  transform: translateX(-50%);
  text-align: center;

  @media (min-width: @tablet-width) {
    position: static;
    top: auto;
    left: auto;
    width: 436px;
    transform: translateX(0);
  }
}

.achievments__content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;

  @media (min-width: @tablet-width) {
    flex-direction: column;
    justify-content: flex-start;
  }

  @media (min-width: @desktop-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

// * custom checkbox

.achievments__group {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  width: 270px;
  margin-bottom: 47px;

  @media (min-width: @desktop-width) {
    width: 260px;
  }
}

.achievments__input:checked ~ .achievments__label::after {
  display: block;
}
