// form-submit

.form-submit__content {
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  padding-top: 10px;
  padding-bottom: 30px;

  @media (min-width: @tablet-width) {
    flex-direction: row;
    justify-content: space-between;
  }
}

.form-submit__button {
  font-weight: 700;
}

.form-submit__requred {
  font-size: 0.875em;
  line-height: 2.14;
  text-transform: uppercase;

  span:last-child {
    display: none;

    @media (min-width: @desktop-width) {
      display: inline;
    }
  }
}
